@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter.ttf");
  font-style: normal;
}
.header {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  min-height: 80px;
  display: flex;
  align-items: center;
  background-color: rgb(255, 255, 255);
}
.header__logo {
  display: flex;
  align-items: center;
  margin-right: auto;
}
.header__logo-img {
  width: 200px;
  height: 40px;
  object-fit: contain;
}
.header__content {
  display: flex;
  align-items: center;
  gap: 32px;
  max-width: 1200px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0 24px;
}
.header__links {
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;
}
.header__link {
  display: flex;
  padding: 8px 16px;
  border-radius: 99px;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: inherit;
  text-decoration: none;
  transition: all 0.2s;
}
.header__link:hover {
  background-color: #f7dee8;
}
.header__link:active {
  background-color: #d8598c;
  color: #fff;
}

.footer {
  display: flex;
  align-items: center;
  margin-top: auto;
  background-color: #2f2227;
}
.footer__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
  max-width: 1200px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 64px 24px;
}
@media (max-width: 1199px) {
  .footer__content {
    gap: 24px;
    padding: 24px;
  }
}
@media (max-width: 767px) {
  .footer__content {
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding: 16px;
  }
}
.footer__block {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
@media (max-width: 1199px) {
  .footer__block {
    padding: 16px 0;
  }
}
.footer__logo {
  display: flex;
  align-items: center;
}
.footer__logo-img {
  width: 160px;
  height: 32px;
  object-fit: contain;
}
.footer__copyright {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: rgba(255, 255, 255, 0.72);
}
.footer__links {
  display: flex;
  flex-direction: column;
  gap: 2px;
  padding: 8px 0;
  margin: 0;
  list-style: none;
}
.footer__links-element {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 36px;
}
.footer__links-element::after {
  position: absolute;
  content: "";
  left: 0;
  top: 15px;
  width: 24px;
  height: 2px;
  border-radius: 10px;
  background-color: #d8598c;
}
.footer__link {
  color: #fff;
  text-decoration: none;
  cursor: pointer;
}
.footer__text {
  font-size: 20px;
  line-height: 32px;
  color: #fff;
}
.footer__form {
  display: flex;
  gap: 16px;
  align-items: flex-start;
}

.accordion {
  display: flex;
  flex-direction: column;
  gap: 2px;
}
.accordion__item {
  padding: 32px;
  border-radius: 24px;
  background-color: #fbeef4;
  transition: background-color 0.2s;
}
@media (max-width: 767px) {
  .accordion__item {
    padding: 24px;
  }
}
@media (max-width: 767px) {
  .accordion__item {
    padding: 16px;
  }
}
.accordion__item_show, .accordion__item_slidedown {
  background-color: #f7dee8;
}
.accordion__header {
  display: flex;
  justify-content: space-between;
  gap: 32px;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: -0.48px;
  cursor: pointer;
  transition: all 0.2s;
}
@media (max-width: 767px) {
  .accordion__header {
    gap: 16px;
    font-size: 16px;
    line-height: 24px;
  }
}
.accordion__header-text {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media (max-width: 767px) {
  .accordion__item-number {
    display: none;
  }
}
.accordion__header-icon-wrapper {
  align-self: flex-start;
  flex-shrink: 0;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.2s;
}
@media (max-width: 767px) {
  .accordion__header-icon-wrapper {
    width: 40px;
    height: 40px;
  }
}
.accordion__icon {
  width: 32px;
  height: 32px;
  color: #2f2227;
  transition: all 0.2s;
}
@media (max-width: 767px) {
  .accordion__icon {
    width: 24px;
    height: 24px;
  }
}
.accordion__icon_minus {
  display: none;
}
.accordion__icon path {
  transition: fill 0.2s;
}

.accordion__item_show .accordion__icon_plus {
  display: none;
}
.accordion__item_show .accordion__icon_minus {
  display: block;
}

.accordion__header:hover .accordion__header-icon-wrapper {
  background-color: #f7dee8;
}
.accordion__header:active .accordion__header-icon-wrapper {
  background-color: #d8598c;
}
.accordion__header:active .accordion__icon {
  color: #fff;
}

.accordion__content {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-top: 20px;
  margin-left: 44px;
  margin-right: 56px;
}
@media (max-width: 767px) {
  .accordion__content {
    margin-right: 0;
  }
}
.accordion__content ul {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 0;
  margin: 0;
  list-style: none;
}
.accordion__content ul li {
  position: relative;
  padding-left: 36px;
}
@media (max-width: 767px) {
  .accordion__content ul li {
    padding-left: 24px;
  }
}
.accordion__content ul li::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 2px;
  border-radius: 2px;
  background-color: #d8598c;
}
@media (max-width: 767px) {
  .accordion__content ul li::after {
    width: 16px;
    height: 2px;
  }
}

.accordion__item:not(.accordion__item_show) .accordion__body {
  display: none;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 30px;
  margin: 0;
  border-width: 2px;
  border-style: solid;
  border-radius: 99px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s;
}
.button.primary {
  border-color: #d8598c;
  background-color: #d8598c;
  color: #fff;
}
.button.primary:hover:not(:disabled) {
  border-color: #ad4770;
  background-color: #ad4770;
}
.button.primary:active:not(:disabled) {
  border-color: #823554;
  background-color: #823554;
}
.button.primary:disabled {
  opacity: 0.48;
}
.button.secondary {
  background-color: rgb(255, 255, 255);
  border-color: rgba(47, 34, 39, 0.8);
  color: #2f2227;
}
.button.secondary:hover:not(:disabled) {
  border-color: #d8598c;
  background-color: #f7dee8;
}
.button.secondary:active:not(:disabled) {
  border-color: #823554;
  background-color: #823554;
  color: #fff;
}
.button.secondary:disabled {
  opacity: 0.48;
}

.text-input {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__label {
  margin-bottom: 4px;
  margin-left: 2px;
  font-size: 14px;
  line-height: 20px;
  color: rgba(47, 34, 39, 0.8);
}
.text-input__input {
  min-height: 48px;
  padding: 10px 34px 10px 16px;
  border-radius: 4px;
  border: 2px solid rgba(0, 0, 0, 0.16);
  background-color: rgb(255, 255, 255);
  color: inherit;
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  transition: all 0.2s;
}
.text-input__input::placeholder {
  color: rgba(47, 34, 39, 0.56);
}
.text-input__input:hover {
  border-color: rgba(0, 0, 0, 0.24);
}
.text-input__input:focus {
  outline: none;
  border-color: #66a6d4;
}
.text-input__input:disabled {
  border-color: rgba(0, 0, 0, 0.08);
  background-color: rgba(0, 0, 0, 0.08);
}
.text-input__input.error {
  border-color: #ff5459;
}
.text-input__input_force-alert {
  border-color: #ff5459;
}
.text-input__input-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__reset {
  display: none;
}
.text-input__show {
  display: flex;
}
.text-input__reset, .text-input__show {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 8px;
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.text-input__reset:hover svg, .text-input__show:hover svg {
  color: #2f2227;
}
.text-input__reset.active, .text-input__show.active {
  display: flex;
}
.text-input__icon {
  color: #2f2227;
  transition: all 0.2s;
}
.text-input__alert {
  display: none;
  margin-top: 4px;
  color: #ff5459;
  font-size: 14px;
  line-height: 20px;
}
.text-input__alert.active {
  display: block;
}
.text-input__addon {
  margin-top: 4px;
  font-size: 14px;
  line-height: 20px;
}
.text-input__sub-text {
  margin-top: 4px;
  margin-left: 2px;
  color: #fff;
  font-size: 14px;
  line-height: 20px;
}
.text-input__force-alert {
  margin-top: 4px;
  color: #ff5459;
  font-size: 14px;
  line-height: 20px;
}

.card {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.card__img {
  aspect-ratio: 4/3;
  object-fit: contain;
  width: 100%;
  border-radius: 24px;
}
.card__block {
  display: flex;
  gap: 8px;
  padding: 0 8px 16px;
}
.card__icon {
  flex-shrink: 1;
  width: 48px;
  height: 48px;
}
.card__content {
  display: flex;
  flex-direction: column;
  gap: 2px;
  margin-top: 12px;
}
.card__title {
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}
.card__text {
  font-size: 14px;
  line-height: 20px;
}

.card-low {
  display: flex;
  flex-direction: column;
  gap: 16px;
  min-height: 250px;
  padding: 16px 16px 24px;
  border-radius: 24px;
  background-color: rgb(255, 255, 255);
}
.card-low__img {
  width: 64px;
  height: 64px;
}
.card-low__content {
  display: flex;
  flex-direction: column;
  gap: 2px;
}
.card-low__title {
  font-size: 20px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: -0.2px;
}
.card-low__text {
  font-size: 14px;
  line-height: 20px;
}
.card-low__block {
  display: flex;
  gap: 4px;
  margin-top: auto;
}
.card-low__discount {
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: -0.48px;
}
.card-low__discount-img {
  flex-shrink: 1;
  width: 32px;
  height: 32px;
}

.promo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  aspect-ratio: 1440/372;
  background-image: url("../img/bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.card-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 16px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.card-low-list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 16px;
  margin: 0;
  padding: 0;
  list-style: none;
}

* {
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-size: 20px;
  line-height: 1.6;
  font-weight: 400;
  font-family: "Inter", "Helvetica", sans-serif;
  color: #2f2227;
}
@media (max-width: 767px) {
  body {
    font-size: 16px;
    line-height: 24px;
  }
}

.main {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 80px auto 0;
}
@media (max-width: 767px) {
  .main {
    margin-top: 80px;
  }
}

.title1 {
  max-width: 1152px;
  margin: 0 auto;
  text-align: center;
  font-size: 48px;
  font-weight: 900;
  line-height: 58px;
  letter-spacing: -1.92px;
  color: #fff;
}

.title2 {
  margin: 0;
  font-size: 32px;
  font-weight: 400;
  line-height: 44px;
  letter-spacing: -0.96px;
}

.title3 {
  margin: 0;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: -0.48px;
}

.section {
  display: flex;
  flex-direction: column;
  gap: 32px;
  width: 100%;
  padding: calc((100vw - 1152px) / 2);
  padding-top: 64px;
  padding-bottom: 64px;
}
.section_start {
  background: linear-gradient(180deg, #fff 50%, #f9f1f0 100%);
}
.section_center {
  background-color: #f9f1f0;
}
.section_end {
  background: linear-gradient(0deg, #fff 50%, #f9f1f0 100%);
}
@media (max-width: 767px) {
  .section {
    padding: 0 16px;
  }
}

.hidden {
  display: none !important;
}

.transparent {
  opacity: 0 !important;
}

.body-lock {
  overflow: hidden;
}